const alt_word_maker = {
    'q': '',
    'w': '',
    'e': '',
    'r': '',
    't': '',
    'y': '',
    'u': '',
    'i': '',
    'o': '',
    'p': '',
    '[': '',
    ']': '',
    '{': '',
    '}': '',
    'a': '',
    's': '',
    'd': '',
    'f': '',
    'g': '',
    'h': '',
    'j': '',
    'k': '',
    'l': '',
    ';': '',
    ':': '',
    '\'': '',
    '\"': '',
    'z': '',
    'x': '',
    'c': '',
    'v': '',
    'b': '',
    'n': '',
    'm': '',
    ',': '',
    '.': '',
    '`': '',
    '<': '',
    '>': '',
    '~': '',
};

String.prototype.altWordMaker = function() {
    let result = '';
    let text = this.split('');
    for (let i = 0; i < text.length; i++) {
        if (alt_word_maker[text[i].toLowerCase()] !== undefined) {
            result += alt_word_maker[text[i].toLowerCase()];
        }
        else {
            result += text[i];
        }
    }
    return result;
};